@font-face {
  font-family: InstrumentSerif;
  src: url('./Assets/fonts/InstrumentSerif-Regular.ttf');
}

@font-face {
  font-family: RobotoCondensed;
  src: url('./Assets/fonts/RobotoCondensed-Medium.ttf');
  font-weight: 500;
}

@font-face {
  font-family: RobotoCondensed;
  src: url('./Assets/fonts/RobotoCondensed-Regular.ttf');
  font-weight: 400;
}

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  overscroll-behavior: none;
  overscroll-behavior-y: none;
  background-color: #C23105;
}

:root {
  --text-color: rgba(0, 0, 0, 0.75);
  --transparency-type: darken;
  --gap-size: 20px;
  --mobile-gap-size: 10px;
  --desktop-font-size: 40px;
  --mobile-font-size: 30px;
}

.App {
  color: var(--text-color);
  position: relative;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  /* Changed from 100dvh to 100vh */
  box-sizing: border-box;
  padding: var(--gap-size);
  display: grid;
  grid-template-columns: repeat(12, [col-start] 1fr);
  gap: var(--gap-size);
  font-family: InstrumentSerif;
  overflow-y: scroll;
  /* Prevents overflow issues */
}

#backgroundVideoPoster {
  display: block;
  position: fixed;
  right: 0;
  bottom: 0;
  object-fit: cover;
  width: 100%;
  height: 100%;
  z-index: 1;
  transform: translate3d(0, 0, 0);
  z-index: 1;
}

#backgroundVideoDesktop {
  display: block;
  position: fixed;
  left: 70%;
  bottom: 0;
  object-fit: cover;
  width: 155%;
  height: 100%;
  z-index: 2;
  transform: translateX(-50%);
}

#backgroundVideoMobile {
  display: none;
}

#about {
  z-index: 3;
  mix-blend-mode: var(--transparency-type);
  margin: 0;
  grid-column-start: 1;
  grid-column-end: 8;
  font-size: var(--desktop-font-size);
  text-shadow: 0px 0px 1px var(--text-color);
  line-height: calc(var(--desktop-font-size) + 2px);
}

.titleFont {
  font-family: RobotoCondensed;
  font-size: calc(var(--desktop-font-size) + 2px);
  font-weight: 500;
  text-transform: uppercase;
}

.textSpacing {
  letter-spacing: -1px;
}

#nav {
  position: fixed;
  right: var(--gap-size);
  bottom: var(--gap-size);
  z-index: 3;
  mix-blend-mode: var(--transparency-type);
}

#contact {
  margin: 0;
  color: var(--text-color);
  font-size: calc(var(--desktop-font-size) / 1.5);
  text-shadow: 0px 0px 1px var(--text-color);
  text-decoration: underline;
  height: auto;
  transition: .3s;
}


#contact:hover {
  text-shadow: 0px 0px 3px var(--text-color);
}

@media screen and (max-width: 600px) {
  #root {
    overflow-y: scroll;
  }

  .App {
    overflow-x: hidden;
    height: auto;
    padding: var(--mobile-gap-size);
    gap: 0;
  }

  #backgroundVideoPoster {
    position: absolute;
  }

  #backgroundVideoDesktop {
    display: none;
  }

  #backgroundVideoMobile {
    display: block;
    position: absolute;
    left: 170%;
    top: 0;
    object-fit: cover;
    width: 400%;
    height: 100%;
    z-index: 2;
    transform: translateX(-50%);
  }

  #backgroundVideoPoster {
    object-position: 20% center;
  }

  #about {
    z-index: 3;
    mix-blend-mode: var(--transparency-type);
    margin: 0;
    grid-column-start: 1;
    grid-column-end: 10;
    font-size: var(--mobile-font-size);
    text-shadow: 0px 0px 1px var(--text-color);
    line-height: calc(var(--mobile-font-size) + 2px);
  }

  .titleFont {
    font-size: calc(var(--mobile-font-size) + 2px);
  }

  #contact {
    font-size: calc(var(--mobile-font-size) / 1.5);
    margin-top: var(--mobile-gap-size);
  }

  #nav {
    position: static;
    margin: 40px 0 0 20px;
    position: relative;
    grid-column-start: 1;
    grid-column-end: 12;
    z-index: 3;
    mix-blend-mode: var(--transparency-type);
  }
}